/* eslint-disable*/

export const displayMap = locations => {
    mapboxgl.accessToken =
        'pk.eyJ1IjoieWF0cmFhIiwiYSI6ImNsbDU2bXIwdjBkdjkzbnJua2x5M3d0NmMifQ.bv8B-bKl4wPmvG4InNN9uQ';
    var map = new mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/yatraa/cll5zyfv700ks01qp7hbr1he3',
        scrollZoom: false
        // center: [-118.324396, 34.097984],
        // zoom: 4
    });

    const bounds = new mapboxgl.LngLatBounds();

    locations.forEach(loc => {
        //create marker
        const el = document.createElement('div');
        el.className = 'marker';
        //Add marker
        new mapboxgl.Marker({
            element: el,
            anchor: 'bottom'
        })
            .setLngLat(loc.coordinates)
            .addTo(map);

        //Add popup

        new mapboxgl.Popup({
            offset: 30
        })
            .setLngLat(loc.coordinates)
            .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
            .addTo(map);
        //Extend the map bounds to include the current location
        bounds.extend(loc.coordinates);
    });
    map.fitBounds(bounds, {
        padding: {
            top: 200,
            bottom: 150,
            left: 100,
            right: 100
        }
    });
};
